.back-to-top .top {
    position: fixed;
    cursor: pointer;
    bottom: 15px;
    right: 15px;
    font-size: 25px;
    color: #ffffff;
    background-color: #000000;
    z-index: 4;
    width: 45px;
    text-align: center;
    height: 45px;
    line-height: 45px;
    border-radius: 50%;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  
  .back-to-top .top:hover {
    color: #ffffff;
    background: #ff3366;
  }

 @media screen and (max-width:480px) {
    

     
     .top{
         font-size: 25px;
     }
     .back-to-top .top {
         bottom: 45px;
         padding-top: 10px;
     }
 } 
